@font-face {
  font-family: 'PT Mono', monospace;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto Condensed', sans-serif;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto Condensed', sans-serif;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto-Regular';
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

*,
*::before, *::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'PT Mono', 'Arial', monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: normal;
  font-style: normal;
  color: #666666;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1920px;
  margin: 0 auto; }

.center {
  max-width: 1500px;
  margin: 0 auto; }

.center--footer-bot {
  display: flex; }

@media (max-width: 1023px) {
  .center {
    max-width: 770px; }
  .center--footer {
    max-width: none;
    width: 100%; }
  .center--footer-bot {
    display: flex;
    max-width: none; } }

@media (max-width: 767px) {
  .center {
    min-width: 320px; } }

.body__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.body__start-container {
  min-height: 878px;
  background-image: url(../../img/circuit-desktop@1x.png), url(../../img/triangles.svg), url(../../img/waves.svg);
  background-position: 82% 245px, 95px 35px, center;
  background-size: 705px 515px, 1514px 750px, cover;
  background-repeat: no-repeat;
  background-color: #1B2D37;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }

@media (max-width: 1023px) {
  .body__start-container {
    min-height: 685px;
    background-position: 665px 110px, -290px -150px, center;
    background-size: 465px 340px, 1514px 750px, cover; } }

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 230px; }

.logo__bold-text {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-style: normal;
  font-weight: bold;
  color: #ffffff; }

.logo__text {
  margin: 0;
  padding: 0;
  font-family: 'Roboto-Regular', 'Arial', sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-style: normal;
  font-weight: 500;
  color: #788891; }

.logo--footer .logo__bold-text {
  color: #203C42; }

@media (max-width: 766px) {
  .logo {
    width: auto; }
  .logo img {
    width: 38px;
    height: 38px; }
  .logo__bold-text,
  .logo__text {
    font-size: 0; } }

.button {
  padding: 14px 15px;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-style: normal;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px;
  color: #ffffff;
  background-color: transparent; }

.button--start {
  background-color: #00B2FF;
  margin-left: 98px;
  margin-bottom: 160px;
  padding: 18px 17px;
  font-size: 14px;
  line-height: 19px;
  border: none; }

@media (max-width: 1023px) {
  .button--header {
    display: none; }
  .button--start {
    margin-left: 47px; } }

.header {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-right: 80px; }

.header__contacts-container {
  display: flex;
  align-items: center;
  max-width: 1170px;
  margin-left: auto; }

.header__menu {
  display: none; }

.header__mail {
  position: relative;
  padding-left: 40px;
  margin-left: auto;
  line-height: 18px;
  background-image: url(../../img/icon-envelope.svg);
  background-position: 0 center;
  background-repeat: no-repeat;
  color: #ffffff; }

.header__tel {
  position: relative;
  margin-left: 88px;
  margin-right: 38px;
  padding-left: 34px;
  line-height: 18px;
  background-image: url(../../img/icon-phone.svg);
  background-position: 0 center;
  background-repeat: no-repeat;
  color: #ffffff; }

@media (max-width: 1023px) {
  .header {
    padding: 0 25px;
    padding-top: 23px;
    padding-bottom: 13px; }
  .header__contacts-container {
    align-items: top;
    justify-content: space-between;
    max-width: none;
    min-width: 160px; }
  .header__menu {
    display: flex;
    width: 37px;
    height: 13px;
    border-top: 1px solid #ffffff;
    border-bottom: 1px solid #ffffff; }
  .header__mail {
    width: 25px;
    height: 18px;
    margin: 0;
    padding: 0;
    font-size: 0;
    background-position: center; }
  .header__tel {
    width: 24px;
    height: 24px;
    margin: 0;
    padding: 0;
    font-size: 0;
    background-position: center; } }

@media (max-width: 1023px) {
  .header {
    padding-top: 17px;
    padding-right: 15px;
    padding-left: 15px; }
  .header__contacts-container {
    min-width: 137px; } }

.start {
  display: flex;
  padding-top: 173px; }

.start__container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  max-width: 750px; }

.start__title {
  margin: 0;
  margin-top: 58px;
  margin-bottom: 30px;
  padding: 0;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 70px;
  line-height: 94px;
  font-style: normal;
  font-weight: bold;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #FFFFFF; }

.start__subtitle {
  position: relative;
  margin: 0;
  margin-bottom: 45px;
  padding: 0;
  padding-left: 98px;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: 0.12em;
  color: #ffffff; }

.start__subtitle::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 66px;
  height: 2px;
  background: #ffffff; }

.start__scroll-text {
  position: absolute;
  bottom: 35px;
  left: -35px;
  max-width: 150px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.5;
  transform: rotate(-90deg); }

@media (max-width: 1023px) {
  .start {
    padding-top: 0;
    background-position: 170% 15%;
    background-repeat: no-repeat;
    background-size: 465px 340px; }
  .start__container {
    position: relative;
    width: 493px;
    padding-left: 63px; }
  .start__title {
    font-size: 60px;
    line-height: 70px; }
  .start__subtitle {
    width: auto;
    padding-left: 47px;
    font-size: 18px;
    line-height: 28px; }
  .start__subtitle::after {
    width: 33px; }
  .start__scroll-text {
    display: none; } }

.main {
  flex-grow: 1; }

.footer {
  position: relative;
  min-height: 316px;
  padding-top: 60px;
  font-size: 15px;
  line-height: 25px;
  letter-spacing: 0.03em;
  color: #1B2D37;
  background-color: #ffffff; }

.footer__container {
  display: flex; }

.footer__title {
  margin: 0;
  margin-bottom: 29px;
  padding: 0;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-style: normal;
  font-weight: normal;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1B2D37; }

.footer__social-row {
  margin-right: auto;
  padding-right: 20px; }

.footer__social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-top: 53px; }

.footer__social li:not(:last-child) {
  margin-right: 15px; }

.footer__social-link {
  display: block;
  width: 27px;
  height: 27px;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%; }

.footer__social-link--fb {
  background-image: url(../../img/icon-facebook.svg); }

.footer__social-link--o {
  background-image: url(../../img/icon-class.svg); }

.footer__social-link--vk {
  background-image: url(../../img/icon-vk.svg); }

.footer__nav {
  max-width: 555px; }

.footer__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  min-height: 100px; }

.footer__nav-list li {
  width: 245px; }

.footer__nav-list li:nth-child(2n+1) {
  margin-right: 65px; }

.footer__nav-list a {
  color: #1B2D37;
  opacity: 0.5; }

.footer__adress-row {
  margin-left: auto; }

.footer__adress-row p {
  margin: 0;
  padding: 0;
  color: #1B2D37;
  opacity: 0.5; }

.footer__tel-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.footer__tel-list a {
  color: #1B2D37;
  opacity: 0.5; }

.footer__bottom {
  margin-top: 55px;
  padding: 14px 0;
  border-top: 1px solid rgba(98, 105, 98, 0.2); }

.footer__bottom p {
  margin: 0;
  margin-right: 60px;
  padding: 0;
  font-size: 13px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1B2D37;
  opacity: 0.5; }

.footer__bottom .footer__sd {
  margin-right: 140px; }

.footer__bottom p:last-child {
  margin-left: auto;
  margin-right: 0; }

@media (max-width: 1023px) {
  .footer {
    min-height: 456px;
    padding-top: 0; }
  .footer__container {
    flex-direction: column; }
  .footer__social-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 0;
    padding: 20px 25px 25px 25px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2); }
  .footer__social {
    margin: 0; }
  .footer__nav-container {
    display: flex;
    padding: 38px 25px 33px 25px; }
  .footer__nav {
    max-width: none; }
  .footer__nav-list {
    flex-direction: column; }
  .footer__nav-list li:nth-child(2n+1) {
    margin-right: 0; }
  .footer__nav-list li {
    width: auto; }
  .footer__bottom {
    margin-top: 0;
    padding: 21px 25px 24px 25px; }
  .footer__bottom .footer__rights {
    font-size: 0;
    margin: 0; }
  .footer__bottom .footer__sd {
    position: absolute;
    top: 35px;
    left: 42%;
    margin: 0; } }
